<style>
    :root {
        --underline-animation-time: 1s;
    }

    #welcome {
        display: grid;
        align-content: center;
        justify-content: center;

        /* Todo: Find way to color based on variable */
        background-image: url("./../background_waves.svg");
        background-position: 0% 50%;
        background-size: cover;
        background-repeat: repeat;
        background-color: rgba(0, 0, 0, 0.25);
    }

    #logo {
        margin: auto;
        width: 20rem;
        height: 20rem;
    }

    #logo path {
        fill: var(--button-foreground-color);
    }

    #h2-container {
        text-align: center;
        margin: auto;
    }

    h2 {
        font-size: 3rem;
        overflow: hidden;
    }

    #hey::after {
        content: "👋";
        margin-left: 1rem;
        display: inline-block;
        animation: 1s wave-animation calc(var(--underline-animation-time) + 500ms) infinite;
    }

    #hook {
        font-size: 1.5rem;
        position: relative;
    }

    #hook::after {
        content: "";
        position: absolute;
        width: 75%;
        height: 3px;
        transform: scaleX(0);
        bottom: -20%;
        left: 12.5%;
        background-color: var(--button-foreground-color);
        transform-origin: center;
        animation: var(--underline-animation-time) underline-animation 500ms forwards;
    }
    
    @keyframes underline-animation {
        to {
            transform: scaleX(1);
        }
    }

    @keyframes wave-animation {
        from {
            transform: rotate(0deg);
        }

        50% {
            transform: rotate(21deg);
        }

        to {
            transform: rotate(0deg);
        }
    }
</style>


